import axios from 'axios';
import {Cities} from '../../templates/Cities';
import {createAsyncThunk} from '@reduxjs/toolkit';

export const fetchCities = createAsyncThunk(
    'city/fetchAll',
    async (value: string, thunkAPI) => {
        try {
            const response = await axios.get<Cities[]>(`https://htmlweb.ru/geo/api.php?json&city_name=${value}&api_key=c0ce976f81633aafb700594fe97582e6`)
            return response.data
        } catch (e) {
            return thunkAPI.rejectWithValue('–û—à–∏–±–∫–∞ —Å–µ—Ä–≤–µ—Ä–∞')
        }

    }
)
export const fetchWeatherCities = createAsyncThunk(
    'city/fetch',
    async (newName: any, thunkAPI) => {
        try {
            const response = await axios.get<Cities[]>(`https://api.openweathermap.org/data/2.5/weather?q=${newName.name},${newName.country}&appid=634d06f1747beb716b919ed5b586d4d0`)
            return response.data;
        } catch (e) {
            return thunkAPI.rejectWithValue('–°–µ—Ä–≤–∏—Å –Ω–µ –º–æ–∂–µ—Ç –Ω–∞–π—Ç–∏ –≥–æ—Ä–æ–¥, –≤–≤–µ–¥–∏—Ç–µ –Ω–∞–∑–≤–∞–Ω–∏–µ –¥—Ä—É–≥–æ–≥–æ –≥–æ—Ä–æ–¥–∞')
        }

    }
)

